import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { VoucherApplicateBean } from '../bean/voucher-applicate.bean';

@Component({
  selector: 'applyPerson',
  templateUrl: './applyPerson.component.html',
  styleUrls: ['./applyPerson.component.scss']
})
export class ApplyPersonComponent implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: string;
  voucherApplicateBean: VoucherApplicateBean = new VoucherApplicateBean();
  first: number = 0;
  tableData: any;
  totalNum: number = 0;
  personArr: any = [];
  msgs: Message[] = [];
  tabMesg: any = tableMessage;
  selectedCar2: any;

  flag: string; 
  tellerName: string;
  tellerId: string;
  orgId:string;
  pageSize: number;
  pageNum: number;
  constructor(
    public httpService: CusOperationHttpService,
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.orgId=this.inValue['orgId'];
    this.pageSize = 10;
    this.pageNum = 1;
    this.query();
  }

  // 点击查询
  queryScale() {
    this.pageSize = 10;
    this.pageNum = 1;
    this.first = 0;
    this.query();
  }

  //重置
  reset(){
    this.tellerId='';
    this.tellerName='';
  }
  
  // 分页查询
  query() {
    let param = {
      pageSize: this.pageSize,
      pageNum: this.pageNum,
      tellerName: this.tellerName,
      tellerId: this.tellerId,
      partyId:this.orgId,
      flag:'1'//0-本机构  1-本机构及下属机构
    }
    this.httpService.queryTellersByPartyIdAndRoleId(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        let tmpTotal = data.total;
        this.totalNum = tmpTotal * 1;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 分页
  paginate(event) {
    this.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    this.first = (currentPage - 1) * this.pageSize;
    this.query();
  }

  // 选中一行
  onRowSelect(event) {
    this.personArr = [];
    this.personArr.push(event.data)
    this.outValue.emit(this.personArr);
  }

}
