import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { VoucherManageBean } from '../bean/voucher-manage.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { VoucherHttpService } from '../../http/voucher.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { reject } from 'q';
import { ProductManageHttpService } from '../../../../../product-manage/http/product-manage.http.service';
@Component({
  selector: 'app-voucher-add',
  templateUrl: './voucher-add.component.html',
  styleUrls: ['./voucher-add.component.scss'],
  providers: [ConfirmationService, ProductManageHttpService, Commfunc]
})
export class VoucherAddComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  voucherManageBean: VoucherManageBean = new VoucherManageBean();
  headerTitle: string//模态框标题
  tableData: any[] = [];//表格数据
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  statusId: any[] = [];//状态
  productRule: any[] = [];//产品规则
  userform: FormGroup; //校验
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  //公共模块 字段所属模块
  code: any;
  display: boolean;
  showModel: string;
  grantEndTime: string;
  displayTree: boolean;//产品列表
  treeData: any;
  selectedFiles: any;
  selectedList: Array<any>;
  baseProduct: any;
  sumProduct: any;
  proFlag: boolean;
  allSelectedList: any[] = [];//全部选中的值
  proportion: string;//抵用比例
  couponAmount: string;//面值
  allProductList: Array<any> = [];//选择的产品列表
  temporaryProList: Array<any> = []//临时列表
  proList: Array<any> = [];
  proNameList: Array<any> = [];
  orgName: string;
  constructor(

    public voucherHttpService: VoucherHttpService,
    public productPlatformHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc

  ) {
    this.codeValues() //调用方法，获取全部码值
    this.statusId = this.code['StatusId2'];
    this.productRule = this.code['ProductRule'];

    // this.statusId = [
    //   { label: '有效', value: '0' },
    // ]
    // this.productRule = [
    //   { label: '全部产品', value: '0' },
    //   { label: '部分产品', value: '1' }
    // ]
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'couponTypeName': new FormControl('', [Validators.required]),
      'couponAmount': new FormControl('', [Validators.pattern(/^[1-9]\d*(\.\d{1,2})?$/), Validators.required]),
      'validity': new FormControl('', [Validators.pattern(/^[1-9]\d*$/), Validators.required]),
      'branchId': new FormControl('', [Validators.required]),
      'productRule': new FormControl('', [Validators.required]),
      // 'productCode': new FormControl('', [Validators.required]),
      'proportion': new FormControl('', [Validators.pattern(/^100$|^[1-9]\d{0,1}(\.\d{1,2})?$/),Validators.required]),
      'grantEndTime': new FormControl('', [Validators.required]),
      'onceNum': new FormControl('', [Validators.pattern(/^[1-9]\d*$/), Validators.required]),
      'num': new FormControl('', [Validators.pattern(/^[1-9]\d*$/), Validators.required]),
      // 'statusId': new FormControl('', [Validators.required]),
    });
    this.voucherManageBean.statusId = '0';
    if (this.voucherManageBean.productRule == '0') {
      this.proFlag = false;
    }
  }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code = ret //code是自己声明的变量
}
  //产品规则选择全部隐藏产品列表
  change() {
    if (this.voucherManageBean.productRule == '0') {
      this.proFlag = false;
    } else {
      this.proFlag = true;
    }
  }

  productList() {
    this.displayTree = true;
    this.query_line();
  }
  //产品列表
  query_line() {
    this.productPlatformHttpService.productCategoryLineQueryList({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        let tempArr = [];
        data.productLineList.forEach(item => {
          let tempObj = { id: 'line' };
          tempObj['label'] = item['productLineName'];
          tempObj['value'] = item;
          tempObj['children'] = [{}];
          tempArr.push(tempObj)
        });
        this.treeData = tempArr;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //+号点击
  nodeExpend(event) {
    if (event.node.id == "line") {
      let arr = [];
      this.productPlatformHttpService.productCategoryGroupInfoQueryList({ productLineId: event.node.value.productLineId }).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.productGroupInfoList.forEach(item => {
            let tempObj = { id: 'group' };
            tempObj['label'] = item['groupName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr;
          if (this.selectedList) {
            if (this.selectedList.indexOf(event.node.label) == -1) {
              this.unSelectCheck(arr, this)
            } else {
              this.selectCheck(arr, this);
            }
          }
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "group") {
      let param = {
        groupId: event.node.value.groupId
      }
      let arr = [];
      this.productPlatformHttpService.pdGroupTemplateQuery(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.groupTempList.forEach(item => {
            let tempObj = { id: 'jichu' };
            tempObj['label'] = item['tplName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr;
          if (this.selectedList) {
            if (this.selectedList.indexOf(event.node.label) == -1) {
              this.unSelectCheck(arr, this)
            } else {
              this.selectCheck(arr, this);
            }
          }
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "jichu") {
      let arr = [];
      event.node.children = this.nodeCheck(event, event.node);
    }
  }

  //选择的产品
  selected(list, selectName) {
    if (this.selectedList) {
      let a = this.selectedList.indexOf(selectName);
      if (a == -1) {//如果父级去掉勾选将子集在this.allProductList删除
        this.temporaryProList.forEach(item => {
          for (let j = 0; j < this.allProductList.length; j++) {
            if (item.productName == this.allProductList[j].productName) {
              this.allProductList.splice(j, 1);
            }
          }
        })
        console.log(this.allProductList);
      } else {//父级勾选，将子集添加到this.allProductList中，并去除重复
        this.allProductList = this.removeRep(this.allProductList.concat(list));
        console.log(this.allProductList);
      }
    }

  }
  //查询出来的产品全部添加到一个list里面，然后去除重复，取消勾选时再去掉相应的产品
  //基础查可售
  baseQuery(data, node, selectName) {
    let arr = [];
    this.productPlatformHttpService.findAvailableProductList(data).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.temporaryProList = data.resultList;
        this.selected(data.resultList, selectName)
        if (node.id == "jichu") {
          this.baseProduct = data.resultList;
          this.baseProduct.forEach(item => {
            let tempObj = { id: 'product' };
            tempObj['label'] = item['productName'];
            tempObj['productName'] = item['productName'];
            tempObj['productId'] = item['productId']
            arr.push(tempObj)
          });
          //判断基础产品有无勾选，有就将子集勾选上，否则就去掉子集勾选
          if (this.selectedList) {
            if (this.selectedList.indexOf(node.label) == -1) {
              this.unSelectCheck(arr, this)
            } else {
              this.selectCheck(arr, this);
            }
          }
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    return arr;
  }
  //点击层级
  nodeCheck(event, node) {
    let baseProduct = [];
    if (event.node.id == "line") {
      let lineID = event.node.value.productLineId;
      let valueProduct = {
        productLineId: lineID,//产品线ID
        productGroupId: '',//产品组id
        productTemplateId: '',//基础产品id
        pageSize: 100,
      }
      this.baseQuery(valueProduct, node, event.node.label);
    }
    if (event.node.id == "group") {
      let groupID = event.node.value.groupId;
      let lineID = event.node.value.productLineId;
      let valueProduct = {
        productLineId: lineID,//产品线ID
        productGroupId: groupID,//产品组id
        productTemplateId: '',//基础产品id
        pageSize: 100,
      }
      this.baseQuery(valueProduct, node, event.node.label);
    }
    if (event.node.id == "jichu") {
      let baseID = event.node.value.tplId;
      let valueProduct = {
        productLineId: '',//产品线ID
        productGroupId: '',//产品组id
        productTemplateId: baseID,//基础产品id
        pageSize: 100,
      }
      baseProduct = this.baseQuery(valueProduct, node, event.node.label);

    }
    if (event.node.id == "product") {
      this.temporaryProList = [event.node];
      let node = [event.node]
      this.selected(node, event.node.productName)
    }
    return baseProduct;

  }
  //产品树
  onChange(event, node) {
    this.expandAfteronChange(node)
  }

  //去重
  removeRep(arr) {
    let n = [];
    for (let i = 0; i < arr.length; i++) {
      let flag = true;
      for (let j = 0; j < n.length; j++) {
        if (arr[i].productName == n[j].productName) {
          flag = false;
        }
      }
      if (flag) {
        n.push(arr[i]);
      }
    }
    return n;
  }

  //产品列表确定
  proClick() {
    this.allProductList.forEach(item => {
      this.proList.push(item.productId);
      this.proNameList.push(item.productName)
    })//取出产品名并转换成string
    let toStr = this.proList.join('|');
    this.voucherManageBean.productCode = toStr.length > 0 ? '|' + toStr + '|' : "";
    this.displayTree = false;
  }
  //产品列表取消
  proCloseBtn() {
    this.displayTree = false;
  }

  //全选
  expandAfteronChange(node) {
    if (this.selectedList.length > 0) {
      //选中
      if (this.selectedList.indexOf(node.label) != -1) {
        // this.allSelectedList.push(node);
        //选中所有子的
        if (node.children) {
          this.selectCheck(node.children, this);
        }
      } else {
        //去掉选中
        if (node.children) {
          this.unSelectCheck(node.children, this);
        }
      }
    }
  }
  //选中所有的子
  selectCheck(list, thiss) {
    list.forEach(element => {
      if (thiss.selectedList && thiss.selectedList.indexOf(element.label) == -1) {
        thiss.selectedList.push(element.label);
      }//选中子集
      if (element.children && element.children.length > 0) {
        this.selectCheck(element.children, thiss);
      }
    });
  }

  //去掉全选
  unSelectCheck(list, thiss) {
    list.forEach(element => {
      if (thiss.selectedList.length == 1) {
        if (thiss.selectedList.indexOf(element.label) != -1) {
          thiss.selectedList = []
        }
      } else {
        if (thiss.selectedList.indexOf(element.label) != -1) {
          thiss.selectedList.splice(thiss.selectedList.indexOf(element.label), 1);
        }//去掉子集选中
        if (element.children && element.children.length > 0) {
          this.unSelectCheck(element.children, thiss);
        }
      }

    });
  }

  //机构树选择
  chooseTree() {
    this.display = true;
    this.headerTitle = "请勾选可用机构";
    this.showModel = "1";
  }

  //机构树选择回调
  chooseCall(param) {
    this.display = false;
    this.voucherManageBean.branchId = param[0].orgId;
    this.orgName = param[0].orgName;
  }
  //日期处理
  selectTime() {
    let selectedTime = new Date(this.grantEndTime);
    let nowTime = new Date();
    if (selectedTime.getTime() <= (nowTime.getTime() - 24 * 60 * 60 * 1000)) {
      this.grantEndTime = "";
      this.msgs = [];
      this.msgs.push({ severity: "error", summary: "提示", detail: "截止日期不能需小于当日日期" })
    }
  }
  //确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //产品列表校验
    if (this.proFlag && this.voucherManageBean.productCode == "") {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '产品列表不能为空' });
      return
    }
    this.voucherManageBean.grantEndTime = this.commfunc.transDate(this.grantEndTime)
    this.voucherManageBean.couponAmount = parseFloat(this.couponAmount).toFixed(2)
    this.voucherManageBean.proportion = parseFloat(this.proportion).toFixed(2);
    this.voucherManageBean.branchName = this.orgName;
    let toStr = this.proNameList.join('|');
    this.voucherManageBean.productName = toStr.length > 0 ? '|' + toStr + '|' : "";

    // this.voucherManageBean.productName=this.proNameList.join('|').length > 0 ? '|' + toStr + '|' : "";
    this.voucherHttpService.couponTypeCreate(this.voucherManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(
          {
            display: false,
            message: '新增成功'
          }
        )
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //取消
  modifyCloseBtn() {
    this.outValue.emit(false);
  }
}
