import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { VoucherApplicateBean, Location } from '../bean/voucher-applicate.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { VoucherHttpService } from '../../http/voucher.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { resolve } from 'url';
import { TZB_CUS_UPLOAD } from "app/@uisftech/common/service/http.constant";
import { reject } from 'q';

@Component({
  selector: 'app-applicate-add',
  templateUrl: './applicate-add.component.html',
  styleUrls: ['./applicate-add.component.scss'],
  providers: [ConfirmationService, Commfunc, CusOperationHttpService, CusViewHttpService, VoucherHttpService]
})

export class ApplicateAddComponent implements OnInit {
  preserveFlag: boolean = false;
  @Input() locationValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  voucherApplicateBean: VoucherApplicateBean = new VoucherApplicateBean();
  location: Location = new Location();
  userform: FormGroup; //校验
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  tableData: any[] = [];//列表数据
  selected: any[] = [];//抵用券类型选择列表
  inp: number;//数量
  UPLOAD: string = TZB_CUS_UPLOAD;//上传路径
  upLoadFiles: any[] = [];//上传文件名
  checkBoxAll: any;//全选
  checkFlag: boolean;
  infoList: any[] = [];//上传文件路径
  chName: any;
  mesg: any;
  dealPerShow: boolean;
  waitSelUserList: any;
  outVal: any;

  constructor(
    public voucherHttpService: VoucherHttpService,
    public cusOperationHttpService: CusOperationHttpService,
    public cusViewHttpService: CusViewHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    this.userform = this.fb.group({
      'partyId': new FormControl('', [Validators.required]),
      'applyReason': new FormControl('', [Validators.required]),
    });
    this.voucherTypeQuewry();
  }

  //抵用券类型查询
  voucherTypeQuewry() {
    let param = {
      statusId: '0',
      branchId: this.locationValue
    }
    this.voucherHttpService.couponTypeQuery(param).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.tableData = this.commfunc.typeListProcess(data.couponTypeList);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  reduce(num, index) {
    if (this.tableData[index].number) {
      num--;
      this.tableData[index].number = String(num);
      if (num < 1) {
        this.tableData[index].number = String(1);
      } else {
        this.tableData[index].number = String(num);
      }
    } else {
      this.tableData[index].number = String(1);
    }
  }
  add(num, index) {
    if (this.tableData[index].number) {
      num++;
      this.tableData[index].number = String(num);
    } else {
      this.tableData[index].number = String(1);
    }
  }
  //申请数量
  numChange(event, car) {
    let val = event.srcElement.value;
    if (!(/^[1-9]\d*$/.test(val))) {
      event.srcElement.value = "";
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '申请数量只能为正整数' });
    } else {
      if (Number(val) > Number(car.onceNum)) {
        event.srcElement.value = "";
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '申请数量不能大于单次申请数量上限' });
      }
      if (Number(val) > Number(car.surNum)) {
        event.srcElement.value = "";
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '申请数量不能大于剩余总数量' });
      }

    }

  }



  //选中的类型列表处理
  process() {
    let listNo = []
    this.selected.forEach(ri => {
      let obj = {};
      obj['couponTypeId'] = this.tableData[ri].couponTypeId;
      obj['number'] = this.tableData[ri].number;
      obj['grantEndTime'] = this.commfunc.getCurrentDate();
      listNo.push(obj)
    });
    return listNo;
  }

  //客户名称查询
  doChange() {
    this.voucherApplicateBean.partyId = this.voucherApplicateBean.partyId.replace(/(^\s*)|(\s*$)/g, '');
    let param = {
      custNo: this.voucherApplicateBean.partyId,
      principalLinkman: this.mesg["userId"]
    }
    this.cusOperationHttpService.selectCustBriefInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.voucherApplicateBean.partyName = data.custName;
      }
    })
  }

  //存款&贷款查询
  doChange1() {
    this.voucherApplicateBean.partyIdTo = this.voucherApplicateBean.partyIdTo.replace(/(^\s*)|(\s*$)/g, '');
    let param = {
      custNo: this.voucherApplicateBean.partyIdTo
    }
    this.cusViewHttpService.selectCustBriefAmtInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.voucherApplicateBean.depositBalanceTo = data.depositsOutstand;
        this.voucherApplicateBean.loanBalanceTo = data.creditBalance;
      }
    })
    let param1 = {
      custNo: this.voucherApplicateBean.partyIdTo,
      principalLinkman: this.mesg["userId"]
    }
    this.cusOperationHttpService.selectCustBriefInfo(param1).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.voucherApplicateBean.partyIdToName = data.custName;
      }
    })
  }

  //点击上传
  load(event) {
    if (JSON.parse(event.xhr.response).success == false) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
      return
    }
    for (const file of event.files) {
      this.upLoadFiles.push(file)
    }
    let infoList = JSON.parse(event.xhr.response);
    let obj = {};
    for (let item of infoList) {
      obj["filePath"] = item.fileUrl + item.fileName;
      this.infoList.push(obj);
    }
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '文件上传', detail: '上传成功' });
  }
  //多行列表全选按钮
  checkBoxAllClick() {
    let list = [];
    if (this.tableData) {
      if (this.checkBoxAll) {
        for (let i = 0; i < this.tableData.length; i++) {
          if (list.indexOf(i) == -1) {
            list.push(i);
          }
        }
      } else {
        list = [];
      }
    }
    this.selected = list;
  }

  //保存按钮
  preserveClick() {
    this.preSub('0', '保存成功');
    if (this.preserveFlag) {
      this.save('0', '保存成功');
    }
  }

  //提交
  submit() {
    this.preSub('1', '提交成功')
    // if(!this.dealPerShow){
    //   this.preSub('1', '提交成功')
    // }
  }


  outValueCall(event) {
    let ss = '';
    event.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    // this.preSub('1', '提交成功')
    this.save('1', '提交成功')
  }

  outDisplayCall(event) {
    this.dealPerShow = event;
  }
  //保存和提交封装
  preSub(flag, msg) {
    // //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //抵用券类型列表非空校验
    if (this.selected.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择抵用券类型' });
      return
    }
    let thiss = this
    let d = new Promise((resolve, reject) => {
      //主联系人校验
      thiss.cusViewHttpService.selectPartyRelationByAdminDoor({ custList: [{ "custNo": thiss.voucherApplicateBean.partyId }] }).subscribe(data => {
        if (data.returnCode.code == 'success') {
          resolve({ a: data.custList[0].custInfoList })
        } else {
          thiss.msgs = [];
          thiss.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        thiss.msgs = [];
        thiss.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    })

    let a = new Promise((resolve, reject) => {
      //申请数量，发放截至日期校验
      let param = {
        couponTypeList: thiss.process(),
      }
      thiss.voucherHttpService.checkCouponInfo(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          resolve({ b: data.flag })
        } else {
          thiss.msgs = [];
          thiss.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        thiss.msgs = [];
        thiss.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    })

    let b = new Promise((resolve, reject) => {
      //被介绍客户校验
      let param1 = {
        partyIdTo: thiss.voucherApplicateBean.partyIdTo
      }
      thiss.voucherHttpService.checkIntroducedCustomer(param1).subscribe(data => {
        if (data.returnCode.code == 'success') {
          resolve({ c: data.flag })
        } else {
          thiss.msgs = [];
          thiss.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        thiss.msgs = [];
        thiss.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    })

    //a、b、d全部响应成功后执行c
    let c = Promise.all([d, a, b]);
    c.then((res) => {
      let firstRes = res[0]['a']
      let custManagerNosFlag = false;
      let custManagerTypeFlag = false;
      //非主联系人校验
      for (let i = 0; i < firstRes.length; i++) {
        if (firstRes[i]['custManagerNos'] == '0301') {
          custManagerNosFlag = true;
          let custManagerTypeList = firstRes[i]['custManagerType'] ? firstRes[i]['custManagerType'].split('|') : [];
          for (let j = 0; j < custManagerTypeList.length; j++) {
            if (custManagerTypeList[j] == this.mesg['userId']) {
              custManagerTypeFlag = true;
              break;
            }
          }
          break;
        }
      }

      if (!custManagerNosFlag || !custManagerTypeFlag) {
        thiss.msgs = [];
        thiss.msgs.push({ severity: 'error', summary: '提示', detail: '非主联系人不允许申请' });
        return
      }

      //申请数量、发放截至日期、被介绍客户好不通过禁止提交和保存
      if (res[1]['b'] != '0' || res[2]['c'] != '0') {
        return
      }

      // 被介绍客户号不能与客户号相同校验
      if (this.voucherApplicateBean.partyId == this.voucherApplicateBean.partyIdTo) {
        thiss.msgs = [];
        thiss.msgs.push({ severity: 'error', summary: '提示', detail: '被介绍客户号不能与客户号相同' });
        return
      }
      if (flag == '1') {
        thiss.waitSelUserList = this.commfunc.query('DYQTYLC', {});
        thiss.dealPerShow = this.waitSelUserList.display;
        thiss.outVal = this.waitSelUserList.waitSelUserList;
        if (thiss.waitSelUserList.erro) {
          thiss.msgs = [];
          thiss.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }
      } else {
        this.preserveFlag = true
      }
    })

  }
  save(flag, msg) {
    this.voucherApplicateBean.personName = this.chName['userName'];
    this.voucherApplicateBean.branchIdName = this.chName['orgName']
    this.voucherApplicateBean.flag = flag;
    this.voucherApplicateBean.couponTypeList = this.process();
    this.voucherApplicateBean.infoList = this.infoList;
    this.voucherHttpService.createCouponApplyInfo(this.voucherApplicateBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit({
          display: false,
          message: msg
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //取消
  modifyCloseBtn() {
    this.outValue.emit({ display: false });
  }




}
