import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { VoucherManageBean } from '../bean/voucher-manage.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { VoucherHttpService } from '../../http/voucher.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-voucher-update',
  templateUrl: './voucher-update.component.html',
  styleUrls: ['./voucher-update.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class VoucherUpdateComponent implements OnInit {
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  voucherManageBean: VoucherManageBean = new VoucherManageBean()
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  statusId: any = [];//状态
  productRule: any[] = [];//产品规则
  grantEndTime: any;//截至日期
  userform: FormGroup; //校验
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  //公共模块 字段所属模块
  code: any;
  display: boolean;
  showModel: string;
  headerTitle: string;
  proFlag: boolean;
  constructor(
    public voucherHttpService: VoucherHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.statusId = this.code['StatusId2'];
    this.productRule = this.code['ProductRule'];



    // this.statusId = [
    //   { label: '有效', value: '0' },
    //   { label: '无效', value: '1' }
    // ]
    // this.productRule = [
    //   { label: '全部产品', vlaue: '0' },
    //   { label: '部分产品', value: '1' }
    // ]
  }

  ngOnInit() {
    this.userform = this.fb.group({
      // 'couponTypeName': new FormControl('', [Validators.required]),
      // 'couponAmount': new FormControl('', [Validators.required]),
      // 'validity': new FormControl('', [Validators.required]),
      // 'branchId': new FormControl('', [Validators.required]),
      // 'productRule': new FormControl('', [Validators.required]),
      // 'productCode': new FormControl('', [Validators.required]),
      // 'proportion': new FormControl('', [Validators.required]),
      'grantEndTime': new FormControl('', [Validators.required]),
      // 'onceNum': new FormControl('', [Validators.required]),
      // 'num': new FormControl('', [Validators.required]),
      // 'statusId': new FormControl('', [Validators.required]),
    });
    this.voucherManageBean = this.inValue;
    let time = this.voucherManageBean.grantEndTime
    this.grantEndTime = new Date(Number(time.slice(0, 4)), Number(time.slice(4, 6)) - 1, Number(time.slice(-2)));

    if (this.voucherManageBean.productRule == '1') {
      this.proFlag = true;
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //产品规则选择全部隐藏产品列表
  change() {
    if (this.voucherManageBean.productRule == '0') {
      this.proFlag = false;
    }
  }

  //机构树选择
  chooseTree() {
    this.display = true;
    this.headerTitle = "请勾选可用机构";
    this.showModel = "1";
  }

  //机构树选择回调
  chooseCall(param) {
    this.display = false;
    this.voucherManageBean.branchId = param.join(',');
  }

  //时间处理
  selectTime() {
    let selectedTime = new Date(this.grantEndTime);
    let nowTime = new Date();
    if (selectedTime.getTime() <= (nowTime.getTime() - 24 * 60 * 60 * 1000)) {
      this.grantEndTime = "";
      this.msgs = [];
      this.msgs.push({ severity: "error", summary: "提示", detail: "截止日期不能小于当前日期" })
    }
  }

  //状态有效校验
  changeToEff() {
    if (this.voucherManageBean.statusId == '0') {
      let now = new Date();
      let nowTime = new Date(now.getFullYear(), now.getMonth(), now.getDate())
      if (this.grantEndTime < nowTime) {
        this.grantEndTime = "";
        this.msgs = [];
        this.msgs.push({ severity: "error", summary: "提示", detail: "截止日期不能小于当前日期" })
      }
    }
  }

  //确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.voucherManageBean.grantEndTime = this.commfunc.transDate(this.grantEndTime)
    this.voucherHttpService.couponTypeUpdate(this.voucherManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(
          {
            display: false,
            message: '修改成功'
          }
        )
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //取消
  modifyCloseBtn() {
    this.outValue.emit(false);
  }
}
