import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { API } from './voucher.http.constant';
import { TZB_HTTP_ORD } from 'app/@uisftech/common/service/http.constant';
import { HttpService } from 'app/@uisftech/common/service/http.service';

@Injectable()
export class VoucherHttpService {

    constructor(
        public httpService: HttpService
    ) { }
    //抵用券
    queryCustomerDiscountCouponUdsp(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.queryCustomerDiscountCouponUdsp, params)
    }

    //客户抵用券操作
    changeCustomerDiscountCouponStatus(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.changeCustomerDiscountCouponStatus, params)
    }
    //抵用券类型新增
    couponTypeCreate(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.couponTypeCreate, params)
    }
    //抵用券类型查询
    couponTypeQuery(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.couponTypeQuery, params)
    }
    //抵用券类型修改
    couponTypeUpdate(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.couponTypeUpdate, params)
    }
    //抵用券申请查询
    queryCouponApplyInfoList(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.queryCouponApplyInfoList, params)
    }
    //抵用券申请
    createCouponApplyInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.createCouponApplyInfo, params)
    }
    //抵用券申请修改
    modifyCouponApplyInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.modifyCouponApplyInfo, params)
    }
    //抵用券申请删除
    deleteCouponApplyInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.deleteCouponApplyInfo, params)
    }
    //抵用券申请数量、截至日期校验
    checkCouponInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.checkCouponInfo, params)
    }
    //被介绍客户校验
    checkIntroducedCustomer(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.checkIntroducedCustomer, params)
    }
    //抵用券详情
    queryCustRequestType(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_ORD + API.queryCustRequestType, params)
    }
}