import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { VoucherComponent } from './voucher.component';
import { VoucherManageComponent } from './voucher-manage/voucher-manage.component';
import { VoucherApplicateComponent } from './voucher-applicate/voucher-applicate.component';
import { CustomVoucherManageComponent } from './custom-voucher-manage/custom-voucher-manage.component';

const routes: Routes = [
  {
    path: '',
    component: VoucherComponent,
    children: [
      { path: "voucher-manage", component: VoucherManageComponent },
      { path: "voucher-applicate", component: VoucherApplicateComponent },
      { path: "custom-voucher-manage", component: CustomVoucherManageComponent }
    ]
  },
];

export const VoucherRouting:ModuleWithProviders = RouterModule.forChild(routes);
