/**
 * @author 吴仕飞
 */
import { Component, OnInit } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { VoucherManageBean } from './bean/voucher-manage.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { VoucherHttpService } from './../http/voucher.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { CommonHttpService } from '../../../../common/http/common.http.service';
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';
@Component({
  selector: 'app-voucher-manage',
  templateUrl: './voucher-manage.component.html',
  styleUrls: ['./voucher-manage.component.scss'],
  providers: [ConfirmationService, CommonHttpService, ProductManageHttpService, Commfunc]
})
export class VoucherManageComponent implements OnInit {
  voucherManageBean: VoucherManageBean = new VoucherManageBean();
  statusId: any[] = [];//抵用券状态
  productRule: any[] = [];//产品规则
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  tableData: any = [];
  userform: FormGroup; //校验
  msgs: Message[] = [];//提示信息
  first: number = 0;//分页控制
  code: any;
  rows: number;//当前页条数
  currentPage: number;//当前页
  headerTitle: string;//模态框标题
  display: boolean;//模态框显示
  updateValue: any;//修改回显内容
  showModel: string;
  grantEndTime: string;
  total: string;
  orgList: Array<any> = [];//可用机构
  inpFlag: boolean = true;
  branchId: string;
  proFlag: boolean;//产品列表显隐
  displayTree: boolean;//产品树
  treeData: any;//产品树数据
  selectedFile: Object;//选中的产品
  productCode: string;//产品
  constructor(
    public voucherHttpService: VoucherHttpService,
    public confirmationService: ConfirmationService,
    public commonHttpService: CommonHttpService,
    public productPlatformHttpService: ProductManageHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues()
    this.statusId = this.code["StatusId1"];
    this.productRule = this.code['ProductRule'];
    // this.statusId = [
    //   { label: '有效', value: '0' },
    //   { label: '无效', value: '1' }
    // ]
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'couponAmount': new FormControl('', [Validators.pattern(/^[1-9]\d*(\.\d{1,2})?$/)]),
      'validity': new FormControl('', [Validators.pattern(/^[1-9]\d*$/)]),
    });
    this.voucherManageBean.pageSize = 10;
    this.voucherManageBean.pageNum = 1;
    if (this.voucherManageBean.productRule == '0') {
      this.proFlag = false;
    }
    this.query();
    this.orgquery('');
  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //截至时间
  selectTime() {

  }



  
  //产品规则
  proChange() {
    if (this.voucherManageBean.productRule == '1') {
      this.proFlag = true;
    } else {
      this.proFlag = false;
      this.voucherManageBean.productCode = '';
      this.productCode = '';
    }
  }
  productList() {
    this.displayTree = true;
    this.query_line();
  }


  //产品列表
  query_line() {
    this.productPlatformHttpService.productCategoryLineQueryList({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        let tempArr = [];
        data.productLineList.forEach(item => {
          let tempObj = { id: 'line' };
          tempObj['label'] = item['productLineName'];
          tempObj['value'] = item;
          tempObj['children'] = [{}];
          tempArr.push(tempObj)
        });
        this.treeData = tempArr;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //+号点击
  nodeExpend(event) {
    if (event.node.id == "line") {
      let arr = [];
      this.productPlatformHttpService.productCategoryGroupInfoQueryList({ productLineId: event.node.value.productLineId }).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.productGroupInfoList.forEach(item => {
            let tempObj = { id: 'group' };
            tempObj['label'] = item['groupName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr;
        }
      })
    }
    if (event.node.id == "group") {
      let param = {
        groupId: event.node.value.groupId
      }
      let arr = [];
      this.productPlatformHttpService.pdGroupTemplateQuery(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.groupTempList.forEach(item => {
            let tempObj = { id: 'jichu' };
            tempObj['label'] = item['tplName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr;
        }
      })
    }
    if (event.node.id == "jichu") {
      let arr = [];
      event.node.children = this.nodeCheck(event, event.node);
    }
  }

  //基础查可售
  baseQuery(data, node) {
    let arr = [];
    this.productPlatformHttpService.findAvailableProductList(data).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (node.id == "jichu") {
          data.resultList.forEach(item => {
            let tempObj = { id: 'product' };
            tempObj['label'] = item['productName'];
            tempObj['productName'] = item['productName'];
            tempObj['productId'] = item['productId']
            arr.push(tempObj)
          });
        }
      }
    })

    return arr;
  }
  //点击层级
  nodeCheck(event, node) {
    let baseProduct = [];
    if (event.node.id == "jichu") {
      let baseID = event.node.value.tplId;
      let valueProduct = {
        productLineId: '',//产品线ID
        productGroupId: '',//产品组id
        productTemplateId: baseID,//基础产品id
        pageSize: 100,
      }
      baseProduct = this.baseQuery(valueProduct, event.node);
    }
    if (event.node.id == "product") {
      this.productCode = event.node.productName;
      this.voucherManageBean.productCode = event.node.productId;
      this.displayTree = false;
    }
    return baseProduct;

  }

  //查询方法
  query() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.voucherHttpService.couponTypeQuery(this.voucherManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tableData = [];
        data.couponTypeList.forEach(item => {
          if (item.productName == null) {
            item.productName = "全部产品";
            tableData.push(item);
          } else {
            tableData.push(item)
          }
        })
        this.tableData = tableData;;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //点击查询
  queryClick() {
    this.first = 0;
    this.voucherManageBean.pageNum = 1;
    this.voucherManageBean.grantEndTime = this.commfunc.transDate(this.grantEndTime);
    this.query();
  }

  //重置
  resetClick() {
    this.voucherManageBean.couponTypeName = '';
    this.voucherManageBean.couponAmount = null;
    this.voucherManageBean.statusId = '';
    this.voucherManageBean.validity = null;
    this.voucherManageBean.branchId = '';
    this.voucherManageBean.productCode = '';
    this.grantEndTime = '';
    this.voucherManageBean.productCode = '';
    this.productCode = '';
  }

  //可用机构模糊查询
  inpquery(event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let orgName = event.srcElement.value
        this.orgquery(orgName)
      }, 1000)
    }
  }
  //机构模糊查询
  orgquery(orgName) {
    let param = {
      orgName: orgName,
      pageSize: 30,
      OrgStatus: '0'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.orgList = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
      }
    })
  }

  //新增
  add() {
    this.headerTitle = '抵用券类型新增';
    this.display = true;
    this.showModel = '1';
  }

  //新增回调
  addCall(param) {
    this.display = param.display;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: param.Message });
    this.query();
  }

  //修改
  update(item) {
    this.updateValue = item;
    this.display = true;
    this.showModel = '2';
    this.headerTitle = '抵用券类型修改'
  }

  //修改回调
  updateCall(param) {
    this.display = param.display;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: param.message });
    this.query();
  }

  //分页
  paginate(event) {
    this.rows = event.rows;
    //每页显示的条数
    this.voucherManageBean.pageSize = Number(event.rows);
    //当前页
    this.currentPage = event.page + 1;
    this.voucherManageBean.pageNum = this.currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.query();
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
