
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from "@angular/router";

import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { VoucherApplicateBean } from '../bean/voucher-applicate.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { VoucherHttpService } from '../../http/voucher.http.service';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TZB_HTTP_YXKQ } from 'app/@uisftech/common/service/http.constant';

@Component({
  selector: 'app-applicate-detail',
  templateUrl: './applicate-detail.component.html',
  styleUrls: ['./applicate-detail.component.scss'],
  providers: [ConfirmationService, Commfunc, CusViewHttpService,VoucherHttpService]
})
export class ApplicateDetailComponent implements OnInit {

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  voucherApplicateBean: VoucherApplicateBean = new VoucherApplicateBean();
  userform: FormGroup; //校验
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  tableData: any;//列表数据
  selected: any[] = [];//抵用券类型选择列表
  inp: number;//数量
  upLoadFiles: any[] = [];//上传文件名
  checkBoxAll: any;//全选
  partyId: string;
  partyName: string;
  partyIdTo: string;
  partyIdToName: string;
  loanBalanceTo: string;
  depositBalanceTo: string;
  applyReason: string;
  filePath: string;
  couponApplyId: string;
  showFlag: boolean//申请理由隐藏
  applyList:any;//申请信息列表
  constructor(
    public voucherHttpService: VoucherHttpService,
    public cusViewHttpService: CusViewHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public route: ActivatedRoute,
  ) { }

  ngOnInit() {
    if (this.route.snapshot.params['partyId']) {//审批调用
      this.showFlag = false;
      console.log(this.route.snapshot)
      this.partyId = this.route.snapshot.params['partyId'];
      this.partyName = this.route.snapshot.params['partyName'];
      this.couponApplyId=this.route.snapshot.params['couponApplyId']
      this.voucherHttpService.queryCouponApplyInfoList({ couponApplyId: this.couponApplyId }).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.applyList=data.applyList[0];
          this.partyIdTo = data.applyList[0].partyIdTo;
          this.partyIdToName = data.applyList[0].partyIdToName;
          this.applyReason=data.applyList[0].applyReason;
          this.tableData=data.applyList[0].couponTypeList
          this.backView(this.partyIdTo)
        }
      })
      //this.route.snapshot.params['couponApplyId']
      // this.voucherHttpService.queryCustRequestType({ custRequestId: this.route.snapshot.params['couponApplyId'] }).subscribe(data => {
      //   if (data.returnCode.code == 'success') {
      //     this.tableData = data.typeList;
      //   }
      // })
    } else {//详情调用
      this.voucherApplicateBean = this.inValue;
      this.applyList=this.inValue;
      this.showFlag = true;
      this.partyId = this.inValue.partyId
      this.partyName = this.inValue.partyName
      this.partyIdTo = this.inValue.partyIdTo
      this.partyIdToName = this.inValue.partyIdToName
      this.loanBalanceTo = this.inValue.loanBalanceTo
      this.depositBalanceTo = this.inValue.depositBalanceTo
      this.applyReason = this.inValue.applyReason
      this.filePath = this.inValue.filePath
      this.backView(this.partyIdTo);
      // this.query();
      this.tableData=this.inValue.couponTypeList;
    }

  }

  //存款&贷款回显
  backView(custNo) {
    let param = {
      custNo: custNo
    }
    this.cusViewHttpService.selectCustBriefAmtInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.depositBalanceTo = data.depositsOutstand;
        this.loanBalanceTo = data.creditBalance;
      }
    })
  }

  downloadCheck(){
    if (this.applyList.description == null || this.applyList.description == "") {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "暂未上传任何影像" });
      return
    }
    //获取用户名及ID
    //30301180515141949000454
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let orgName = chName.orgName;
    let userName = chName.userName;
    let time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let datas = time.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (datas.length < 2) {
      datas = "0" + datas
    }
    let QUERY_TIME = '' + year + month + this.applyList;
    window.open(TZB_HTTP_YXKQ + "SunIAS_V7/SunIASRequestServlet.do?UID=MOBILE&OrgID=" + orgID + "&OrgName=" + orgName + "&OrgOwner=" + orgID + "&right=0100000&PWD=cyg&UserID=" + userId + "&UserName=" + userName + "&AppID=SA&info1=BUSI_SERIAL_NO:" + this.applyList.description + ";OBJECT_NAME:APP1009;FILELEVEL:66;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:" + QUERY_TIME)
  }
  

  //点击上传
  load(event) {
    for (const file of event.files) {
      this.upLoadFiles.push(file)
    }
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '文件上传', detail: event.xhr.reponse })
  }


  modifyCloseBtn() {
    this.outValue.emit(false);
  }

}
