import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { VoucherApplicateBean } from './bean/voucher-applicate.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { VoucherHttpService } from './../http/voucher.http.service';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { TZB_HTTP_YXKQ } from 'app/@uisftech/common/service/http.constant';

@Component({
  selector: 'app-voucher-applicate',
  templateUrl: './voucher-applicate.component.html',
  styleUrls: ['./voucher-applicate.component.scss'],
  providers: [ConfirmationService, Commfunc, CommonHttpService]
})
export class VoucherApplicateComponent implements OnInit {
  voucherApplicateBean: VoucherApplicateBean = new VoucherApplicateBean();
  userform: FormGroup; //校验
  tableData: any = [];//表格数据
  statusId: any = [];//抵用券状态
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  first: number = 0;//分页控制
  msgs: Message[] = [];
  code: any;
  rows: number;//当前页条数
  currentPage: number;//当前页
  headerTitle: string;//模态框标题
  display: boolean;//模态框显示
  updateValue: any;//修改回显内容
  updateLocationValue: string;
  detailValue: string;//详情回显内容
  addValue: string;
  locationList: string;
  showModel: string;
  personPage: boolean;//申请人
  startTime: any;//申请开始时间
  endTime: any;//申请结束时间
  selected: any;
  total: string;//总页数
  applyPerson: string;//申请人
  commonHeader:Object;
  extParam:Object;
  applyPersonValue:Object;
  applyPersonFlag:boolean;
  constructor(
    public voucherHttpService: VoucherHttpService,
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public router: Router,

  ) {
    this.codeValues();
    this.statusId = this.code["applicateStatusId"]
    // this.statusId = [
    //   { label: '请选择', value: '' },
    //   { label: '已创建', value: '0' },
    //   { label: '已申请', value: '1' },
    //   { label: '已驳回', value: '2' },
    //   { label: '已通过', value: '3' }
    // ]
  }

  ngOnInit() {
    // this.userform = this.fb.group({
    //   'startTime': new FormControl('', [Validators.required]),
    //   'endTime': new FormControl('', [Validators.required]),
    // });
    //查询条件初始化
    this.voucherApplicateBean.pageSize = 10;
    this.voucherApplicateBean.pageNum = 1;

    this.commonHeader=JSON.parse(this.commfunc.getSessionData("commonHeader"));
    this.extParam=this.commonHeader["extParam"];
    if(this.extParam["managePermission"]=="Y"){
      this.applyPersonFlag=true;
    }else{
      this.applyPersonFlag=false;
    }
    this.query();
    this.locationDes();
  }


  //位置服务查询
  locationDes() {
    // let mesg = JSON.parse(this.commfunc.getSessionData("commonHeader"));
    let param = {
      flag: '0',
      orgId: this.commonHeader["orgId"]
    }
    this.commonHttpService.queryOrgByCombConditions(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.locationList = data.orgInfoList[0].location;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    })
  }

  //码值获取
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }


  openOrgTreeClick() {
    this.personPage = !this.personPage;
    this.applyPersonValue=this.extParam;
  }

  //申请人
  perInfor(param) {
    this.personPage = false;
    this.applyPerson = param[0].tellerName;
    this.voucherApplicateBean.applyPerson = param[0].tellerId;
  }

  //日期范围的处理
  selectTime1() {
    this.selectTime();
  }
  selectTime2() {
    this.selectTime();
  }
  selectTime() {
    if (this.startTime && this.endTime) {
      if (this.startTime > this.endTime) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.startTime = null;
          this.endTime = null;
        })
      }
    }
  }

  //查询
  query() {
    // 

    // //校验的
    // for (const i in this.userform.controls) {
    //   this.userform.controls[i].markAsDirty();
    // }
    // //处理是否提交
    // for (const i in this.userform.controls) {
    //   if (!this.userform.controls[i].valid) {
    //     return;
    //   }
    // }
    this.voucherApplicateBean.startTime = this.commfunc.transDate(this.startTime);
    this.voucherApplicateBean.endTime = this.commfunc.transDate(this.endTime);
    this.voucherHttpService.queryCouponApplyInfoList(this.voucherApplicateBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.applyList;
        this.total = data.totalCount;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //点击查询
  queryClick() {
    this.first = 0;
    this.voucherApplicateBean.pageNum = 1;
    if (this.startTime && !this.endTime) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '截至日期不能为空' });
      return
    } else if (!this.startTime && this.endTime) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能为空' });
      return
    }
    this.query();
  }

  //重置
  resetClick() {
    // this.voucherApplicateBean = new VoucherApplicateBean();
    this.voucherApplicateBean.partyId='';
    this.voucherApplicateBean.partyIdTo='';
    this.startTime = '';
    this.endTime = '';
    this.applyPerson='';
    this.voucherApplicateBean.statusId='';
    this.voucherApplicateBean.applyPerson='';
  }

  //修改
  update(item) {
    this.updateValue = item;
    if (item.statusId == '0' || item.statusId == '2') {
      this.updateLocationValue = this.locationList;
      this.display = true;
      this.showModel = '2';
      this.headerTitle = '抵用券申请修改'
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '已申请或已通过的不能修改' })
    }

  }

  //修改回调
  updateCall(param) {
    this.display = param.display;
    this.msgs = [];
    this.msgs.push({ severity: "success", summary: "提示", detail: param.message });
    this.query();
  }

  //申请
  applicate() {
    this.headerTitle = '抵用券申请';
    this.addValue = this.locationList;
    this.display = true;
    this.showModel = '1';
  }

  //申请回调
  applicateCall(param) {
    this.display = param.display;
    if(param.message){
      this.msgs = [];
    this.msgs.push({ severity: "success", summary: "提示", detail: param.message });
    }
    this.query();
  }

  //详情
  detail(item) {
    this.detailValue = item;
    this.display = true;
    this.showModel = '3';
    this.headerTitle = '抵用券申请详情'
  }

  //详情回调
  detailCall(param) {
    this.display = param;
  }

  //删除
  del(item) {
    if (item.statusId == '1' || item.statusId == '3') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '已申请或已通过的不能删除' });
      return
    }
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        let param = { couponApplyId: item.couponApplyId };
        this.voucherHttpService.deleteCouponApplyInfo(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }

  //上传文件查看
  downloadCheck(data) {
    if (data.description == null || data.description == "") {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "暂未上传任何影像" });
      return
    }
    //获取用户名及ID
    //30301180515141949000454
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let orgName = chName.orgName;
    let userName = chName.userName;
    let time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let datas = time.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (datas.length < 2) {
      datas = "0" + datas
    }
    let QUERY_TIME = '' + year + month + data;
    window.open(TZB_HTTP_YXKQ + "SunIAS_V7/SunIASRequestServlet.do?UID=MOBILE&OrgID=" + orgID + "&OrgName=" + orgName + "&OrgOwner=" + orgID + "&right=0100000&PWD=cyg&UserID=" + userId + "&UserName=" + userName + "&AppID=SA&info1=BUSI_SERIAL_NO:" + data.description + ";OBJECT_NAME:APP1009;FILELEVEL:66;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:" + QUERY_TIME)
  }



  //分页
  paginate(event) {
    this.rows = event.rows;
    //每页显示的条数
    this.voucherApplicateBean.pageSize = Number(event.rows);
    //当前页
    this.currentPage = event.page + 1;
    this.voucherApplicateBean.pageNum = this.currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.query();
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
